import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/admin',
    name: 'Admin',
    component: () => import(/* webpackChunkName: "admin" */ '../views/Admin.vue')
  },
  {
    path: '/instructor',
    name: 'Instructor',
    component: () => import(/* webpackChunkName: "instructor" */ '../views/Instructor.vue')
  },
  {
    path: '/trainee',
    name: 'Trainee',
    component: () => import(/* webpackChunkName: "trainee" */ '../views/Trainee.vue')
  },
  {
    path: '/courses',
    name: 'Courses',
    component: () => import(/* webpackChunkName: "courses" */ '../views/Courses.vue')
  },
  {
    path: '/users',
    name: 'Users',
    component: () => import(/* webpackChunkName: "courses" */ '../views/Users.vue')
  },
  {
    path: '/course/:id',
    name: 'Course',
    component: () => import(/* webpackChunkName: "courses" */ '../views/CourseDetail.vue')
  },
  {
    path: '/user/:id',
    name: 'User',
    component: () => import(/* webpackChunkName: "courses" */ '../views/UserDetail.vue')
  }
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
